*{
    margin :0;
    padding:0;
}
.wrapper{
    width:100%;
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 0.5cm;
    padding: 1cm;
    
}
.row1, .row2, .row3, .row4, .row5, .row6{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 0.5cm;
}

.b1{
    padding: 0.5cm;
    border:thin solid grey;
    background-color: rgb(183, 135, 242);
    border-radius: 50%;
    width:1.5cm;
    align-content: center;
    color: #ffffff;
}

.b2{
    padding: 0.5cm;
    border:thin solid grey;
    background-color: rgb(111, 0, 246);
    border-radius: 50%;
    width:1.5cm;
    align-content: center;
    color: #ffffff;
}

.b3{
    padding: 0.5cm;
    border:thin solid grey;
    background-color: rgb(0, 0, 0);
    border-radius: 50%;
    width:1.5cm;
    align-content: center;
    color: #ffffff;
}

.inputText{
    padding: 0.5cm;
    font-size: 20px;
    border: thin solid rgb(141, 141, 141);
    box-shadow: 1.5px, 1.5px rgba(87, 87, 87, 0.486);
}